<template>
  <div>
    <el-dialog :close-on-click-modal="false" title="引用厂家预检单" :visible="true" class="dialogForm" width="1300px" @close="closeDialog">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <!-- <el-form-item label="销售组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
            </el-date-picker>
          </el-form-item>
          <el-form-item label="厂家预检单据号" size="mini">
            <el-input v-model="params.factory_appraisal_sn" placeholder="请输入"></el-input>
          </el-form-item>

          <!-- <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList.options"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="胎号" size="mini">
            <el-input v-model="params.tire_un" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="品牌" size="mini">
            <getBrand v-model='params.brand_id' />
          </el-form-item>

          <el-form-item label="商品名称" size="mini">
            <v-GoodsList :scopes='{row: params}' :vxeParams="{ company_id:mesCompanyID }" :showObj="{goods_id: '商品编号' , goods_name: '商品名称'}" />
          </el-form-item>

          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="450" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="factory_appraisal_sn" width="180" label="厂家鉴定单据号">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="supplier_name" width="150" label="供应商(厂家)">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="brand_name" width="150" label="品牌名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" width="150" label="商品名称">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="num" width="150" label="返胎数量" align="right">
            <!--   3.1返胎数量都是1； -->
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="tire_un" width="150" label="胎号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="factory_status" width="150" label="厂家鉴定结果">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="鉴定时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="200" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" width="150" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog()">取消</el-button>
        <el-button type="primary" @click="dialogSubmitForm()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getData,
  getCompanyListCommon,
  getDepartmentListCommon,
} from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      authority: {},
      goodsList: [],
      radio: "",
      TableData: {
        tableData: [],
      },
      departmentList: {
        options: [],
      },
      loading: false,
      companyList: [],
      brandList: [], //品牌管理
      params: {},
      per_page: [30, 50, 100, 500],
      searchParams: {},
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      timeValue: [],
      dialogmultipleSelection: [], //客户列表
      setDate: [],
    };
  },
  props: ["message", "mesCompanyID"],
  methods: {
    handleSelectionChange(val) {
      this.dialogmultipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.params.supplier_id = this.message;
      this.params.company_id = this.mesCompanyID;
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      getData(this.searchParams);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.params.supplier_id = this.message;

      this.$set(this, "searchParams", {
        params: { supplier_id: this.message, company_id: this.mesCompanyID },
        TableData: this.TableData, //列表数据
        API: "getSupplierAppraisalListPublics", //接口名
        vm: this, //传递的this
      });
      getData(this.searchParams);
    },
    closeDialog() {
      //引用销售出库单据 弹出框取消
      this.$parent.salesDocumentsV = false;
    },
    companyChange(val) {
      getDepartmentListCommon(this, this.params);
      this.$set(this.params, "department_id", "");
    },
    dialogSubmitForm() {
      //返回引用的出库单单据数据
      let This = this;
      var result = this.dialogmultipleSelection.map((item) => {
        return item.factory_appraisal_goods_id;
      });
      this.$axMethod
        .getAppraisalList({
          factory_appraisal_goods_id: result.toString(),
        })
        .then((res) => {
          if (res.data.code == 200) {
            for (const obj of res.data.data) {
              obj.batch = "返胎";
              obj.wareHouselist = [
                {
                  warehouse_id: obj.warehouse_id,
                  warehouse_name: obj.warehouse_name,
                  allocation_id: obj.allocation_id,
                  allocation_name: obj.allocation_name,
                },
              ];
              obj.warehouseObj = {
                warehouse_id: obj.warehouse_id,
                warehouse_name: obj.warehouse_name,
                allocation_id: obj.allocation_id,
                allocation_name: obj.allocation_name,
              };
            }
            this.$set(this.$parent, "TableData", res.data.data);
            this.$parent.salesDocumentsV = false;
          }
        });
    },
  },
  mounted() {
    this.$set(this, "searchParams", {
      params: { supplier_id: this.message, company_id: this.mesCompanyID }, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getSupplierAppraisalListPublics", //接口名
      vm: this, //传递的this
    });
    getData(this.searchParams); //获取销售订单列表
    getCompanyListCommon(this); //获取组织
  },
};
</script>

